import { Link, useNavigate } from "react-router-dom";
import PasswordField from "./PasswordField";
import SignWith from "./SignWith";
import { useRef, useState } from "react";

const SignIn = () => {
    const navigate = useNavigate();
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const checkBox = useRef(null)

    const handleClick = () =>{
        const signinData = {
            username:username,
            password:password
        }

        fetch(localStorage.getItem('api')+'Account/login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(signinData)
        })
        .then(response => response.json())
        .then((data) => {
            localStorage.setItem('username', data.username)
            localStorage.setItem('firstName', data.firstName)
            localStorage.setItem('lastName', data.lastName)
            localStorage.setItem('token', data.token)
            localStorage.setItem('photoUrl', data.photoUrl)
            navigate("/home");
        })
        .catch(error => console.error('Signup error:', error));
    }
    
    return ( 

    <div className="row justify-content-evenly">
        <div className="col-md-5">


            <div className="col-12 my-2 float-start">
                <h2 className="float-start">Sign In</h2>
            </div>

            <div className="col-12 my-2 float-start">
                <p className="float-start">New user? <Link to="/create" className="">Create an account</Link></p>
            </div>

            <div className="col-12 my-2">
                <input
                    className="form-control border border-dark border-2"
                    type="email"
                    placeholder="Username or email"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                />
            </div>
            <PasswordField sendPassword={setPassword}/>
            <div className="col-12 my-2 form-check">
                <input
                    type="checkbox"
                    className="form-check-input border border-dark border-2 text-start"
                    id="check1"
                    name="option1"
                    value="something"
                    ref={checkBox}>
                </input>
                <label className="form-check-label float-start" htmlFor="check1">Keep me signed in</label>
            </div>

            <button className="btn btn-primary col-12 my-2 p-2 border border-dark border-1" onClick={handleClick}>Sign In</button>

            <div className="row my-2"><hr className="col align-self-center"/><p className="col-auto">Or Sign In With</p><hr className="col align-self-center"/></div>

            <SignWith/>


        </div>

        <div className="col-md-4 align-self-center">
            <img className="img-fluid" src="signin.svg" alt="" />
        </div>
    </div>
     );
}
 
export default SignIn;