import { Link , useNavigate} from "react-router-dom";
import SignWith from "./SignWith";
import { useState } from "react";
import PasswordField from "./PasswordField";

const Create = () => {
    const navigate = useNavigate();
    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

    const handleClick = () =>{
        const signupData = {
            firstName:firstName,
            lastName:lastName,
            username:username,
            password:password,
            gender:"0",
            dateOfBirth:"2023-05-05T14:35:43.162Z"
        }

        fetch(localStorage.getItem('api')+'Account/register', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(signupData)
        })
        .then(response => response.json())
        .then((data) => {
            localStorage.setItem('username', data.username)
            localStorage.setItem('firstName', data.firstName)
            localStorage.setItem('lastName', data.lastName)
            localStorage.setItem('token', data.token)
            navigate("/home");
        })
        .catch(error => console.error('Signup error:', error));
    }


    return (

    <div className="row justify-content-evenly">
        <div className="col-md-5">
            <div className="col-12 my-2 float-start">
                <h2 className="float-start">Create An Account</h2>
            </div>
            <div className="col-12 my-2 float-start">
                <p className="float-start">Already a user? <Link to="/signin" className="">Sign In</Link></p>
            </div>
            <div className="row justify-content-evenly my-2">
                <div className="col">
                    <input
                        className="form-control border border-dark border-2"
                        type="first name"
                        placeholder="first name"
                        value={firstName}
                        onChange={(e) => setFirstName(e.target.value)}
                    />
                </div>
                <div className="col">
                    <input
                        className="form-control border border-dark border-2"
                        type="last name"
                        placeholder="last name"
                        value={lastName}
                        onChange={(e) => setLastName(e.target.value)}
                    />
                </div>
            </div>

            <div className="col-12 my-2">
                <input
                    className="form-control border border-dark border-2"
                    type="email"
                    placeholder="Username or email"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                />
            </div>
            <PasswordField sendPassword={setPassword}/>

            <button className="btn btn-primary col-12 my-2 p-2 border border-dark border-1" onClick={handleClick}>Sign Up</button>

            <div className="row my-2"><hr className="col align-self-center"/><p className="col-auto">Or Sign up With</p><hr className="col align-self-center"/></div>

            <SignWith/>

        </div>


        <div className="col-md-5 align-self-center">
            <img className="img-fluid" src="create.svg" alt="" />
        </div>



        
    </div>



     );
}
 
export default Create;