import moment from 'moment';
import React, { useEffect, useRef, useState } from "react";
import WaveSurfer from "wavesurfer.js";

const formWaveSurferOptions = (ref) => ({
  container: ref,
  waveColor: 'white',
  progressColor: '#4353FF',
  cursorColor: '#4353FF',
  barWidth: 3,
  barRadius: 3,
  responsive: true,
  height: 50,
  // If true, normalize by the maximum peak instead of 1.0.
  normalize: true,
  // Use the PeakCache to improve rendering speed of large waveforms.
  partialRender: true,
  fillParent:true,
  hideScrollbar:true
});

export default function VoiceNote2(props) {
  const url= props.url;
  const waveformRef = useRef(null);
  const wavesurfer = useRef(null);
  const [playing, setPlay] = useState(false);
  const [duration, setDuration] = useState(0);
  const [currentTime, setCurrentTime] = useState(0);

  // create new WaveSurfer instance
  // On component mount and when url changes
  useEffect(() => {
    setPlay(false);

    const options = formWaveSurferOptions(waveformRef.current);
    wavesurfer.current = WaveSurfer.create(options);

    wavesurfer.current.load(url);
    // Removes events, elements and disconnects Web Audio nodes.
    // when component unmount
    return () => wavesurfer.current.destroy();
  }, [url]);


  useEffect(() => {
    if(wavesurfer) {

        wavesurfer.current.on('audioprocess', function () {
            setDuration(wavesurfer.current.getDuration());
            setCurrentTime(wavesurfer.current.getCurrentTime());
        });
        wavesurfer.current.on('ready', function () {
            setDuration(wavesurfer.current.getDuration());
        });
        wavesurfer.current.on('finish', function () {
            setPlay(false);
        });
    
    }
  }, [wavesurfer])
  
  const handlePlayPause = () => {
    setPlay(!playing);
    wavesurfer.current.playPause();
  };

  return (
    <div className="d-flex justify-content-between align-items-center">
        <button className="btn btn-transparent text-white" onClick={handlePlayPause}>
            {playing?
                <i className="bi bi-pause-circle" style={{fontSize:"30px"}}></i>
                :
                <i className="bi bi-play-circle" style={{fontSize:"30px"}}></i>
            }
        </button>
        <div className="" style={{width:250,maxWidth:"50vw"}}><div className="mx-2" id="waveform" ref={waveformRef}></div></div>
        <div className="">{playing? moment.utc(currentTime*1000).format('m:ss')
                                    :moment.utc(duration*1000).format('m:ss')}   
        </div>
    </div>
  );
}
