import React, { useState, useEffect, useRef } from "react";

function VoiceNote({insertVoice}) {
  const [recording, setRecording] = useState(false);
  const [audioUrl, setAudioUrl] = useState("");
  const mediaRecorderRef = useRef();


  const startRecording = async () => {
    const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
    const mediaRecorder = new MediaRecorder(stream);
    mediaRecorderRef.current = mediaRecorder;

    const audioChunks = [];
    mediaRecorder.addEventListener("dataavailable", (event) => {
      audioChunks.push(event.data);
    });

    mediaRecorder.addEventListener("stop", () => {
      const audioBlob = new Blob(audioChunks, { type: "audio/ogg,codecs=opus" });
      const audioUrl = URL.createObjectURL(audioBlob);
      setAudioUrl(audioUrl);
      insertVoice(audioUrl);
      stream.getTracks().forEach(track => track.stop());
    });

    mediaRecorder.start();
    setRecording(true);
  };

  const stopRecording = () => {
    mediaRecorderRef.current.stop();
    setRecording(false);
  };

  const handleClick = () => {
    if (recording) {
      stopRecording();
    } else {
      startRecording();
    }
  };

  

  return (
      <div className="d-flex align-items-center" onClick={handleClick}>
        {recording ?
            <img src="micred.svg" alt="mic" height="30px" width="30px" className="mx-2 img-fluid non-draggable"/>
            :
            <img src="mic.svg" alt="mic" height="30px" width="30px" className="mx-2 img-fluid non-draggable"/>}
      </div>
  );
}

export default VoiceNote;
